.friendMessage {
    display: flex;
    flex-direction: column;
    align-self: flex-start;

    margin-left: 50px;
}

.friendImageAndText {
    display: flex;
    flex-direction: row;
}

.friendImageAndText img {
    width: 48px;
    height: 48px;
    flex-shrink: 0;

}

.friendText {
    display: flex;
    flex-direction: column;

    background-color: #0066CC;
}

.friendText:after {

}

.friendName {
    align-self: flex-start;

    color: #FFF;
    font-family: 'Montserrat', sans-serif;
    font-size: 18px;
    font-style: normal;
    font-weight: 600;
}

.friendMessageText {
    color: #FFF;
    font-size: 16px;
    font-style: normal;
    font-weight: 400;
    font-family: 'Montserrat', sans-serif;
}

.friendTime {
    align-self: flex-start;
    margin-left: 10px;

    color: #000;

    font-family: 'Montserrat', sans-serif;
    font-size: 10px;
    font-style: normal;
    font-weight: 600;
}
