
.message {
    display: flex;
    flex-direction: column;
    align-self: flex-end;

    margin-right: 50px;
}

.imageAndText {
    display: flex;
    flex-direction: row-reverse;


}

.imageAndText img {
    width: 48px;
    height: 48px;
    flex-shrink: 0;
}

.text {
    display: flex;
    flex-direction: column;

    background-color: #0066CC;
}

.text:after {

}

.name {
    align-self: flex-end;

    color: #FFF;
    font-family: 'Montserrat', sans-serif;
    font-size: 18px;
    font-style: normal;
    font-weight: 600;
}

.messageText {
    color: #FFF;
    font-size: 16px;
    font-style: normal;
    font-weight: 400;
    font-family: 'Montserrat', sans-serif;

}

.time {
    align-self: flex-end;
    margin-right: 10px;

    color: #000;

    font-family: 'Montserrat', sans-serif;
    font-size: 10px;
    font-style: normal;
    font-weight: 600;
}