.sendForm {
    display: flex;
    margin-top: 70px;
    margin-right: 50px;
    margin-left: 50px;
}

.textarea {
    flex-grow: 1;
    background-color: #F5F7FB;
    border-radius: 20px;



    font-family: 'Montserrat', sans-serif;
    font-size: 14px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
    border: none;
    resize: none;
    padding: 12px;
}

.button {
    margin-left: 100px;
    width: 120px;
    height: 40px;
    border-radius: 20px;

    font-family: 'Montserrat', sans-serif;
    color: #FFF;
    font-size: 16px;
    font-style: normal;
    font-weight: 600;
    line-height: normal;
    background-color: #06C;
}

